* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.change-btn {
  text-align: center;
  min-width: 500px;
  padding-top: 300px;
}

.start-btn {
  letter-spacing: .06em;
  color: #fafafa;
  cursor: pointer;
  width: 100px;
  height: 40px;
  background-color: #2196f3;
  border: 0;
  border-radius: 4px;
  font-size: 13px;
  font-weight: 700;
  line-height: 1.88;
  transition-property: background-color, fill;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.stop-btn {
  letter-spacing: .06em;
  color: #fafafa;
  width: 100px;
  height: 40px;
  cursor: pointer;
  background-color: #2196f3;
  border: 0;
  border-radius: 4px;
  font-size: 13px;
  font-weight: 700;
  line-height: 1.88;
  transition-property: background-color, fill;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.stop-btn:disabled, .start-btn:disabled {
  color: #533f3f;
  cursor: not-allowed;
  background-color: rgba(33, 149, 243, .306);
  box-shadow: 0 2px 2px rgba(0, 0, 0, .15);
}

